import "./about.css";
import Award from "../../img/award.png";
import aboutme from "../../img/about-me.jpg";

const About = () => {
  return (
    <div className="a">
      <div className="a-left">
        <div className="a-card bg"></div>
        <div className="a-card">
          <img
            src={aboutme}
            alt=""
            className="a-img"
          />
        </div>
      </div>
      <div className="a-right">
        <h1 className="a-title">About Me</h1>
        <p className="a-sub">
          I am a Web Developer.I love to write code.I am interested in latest techs that can change the world
        </p>
        <p className="a-desc">
          I have MCA Degree From Guru Jambheshwar University Hisar.I live in Mumbai but basically I am from Hisar,a town in Haryana state inside territory of India
        </p>
        <div className="a-award">
          <img src={Award} alt="" className="a-award-img" />
          <div className="a-award-texts">
            <h4 className="a-award-title">Design Awards</h4>
            <p className="a-award-desc">
              I can make websites that can achieve design awards.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
